html {
  font-size: 100%;
  box-sizing: border-box;
  width: 100vw;
  height: 100vh;
  height: var(--app-height);
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  min-width: 16.5625rem;
  min-height: 40.625rem;
  height: 100vh;
  height: var(--app-height);
  font-family: "Josefin Sans", sans-serif;
  background-color: #e76f51;
  color: #003049;
  letter-spacing: -0.0625rem;
}

ul {
  list-style: none;
}

img {
  width: 100%;
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

:root {
  --app-height: 100%;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 95%;
  max-width: 21.25rem;
  min-width: 16.5625rem;
  height: 90vh;
  min-height: 37.5rem;
  max-height: 40.625rem;
  margin: 0 auto;
  background-color: #fff9e7;
  border-radius: 2.8125rem;
  border: solid #f9c74f 0.5rem;
}
@media (min-width: 48em) {
  .container {
    max-width: 43.75rem;
  }
}

.hidden {
  display: none;
}

@media (min-width: 48em) {
  main {
    display: flex;
    gap: 1.875rem;
  }
}

.btn {
  font-family: "Croissant One", cursive;
  letter-spacing: -0.125rem;
  color: #003049;
  border-radius: 1.875rem;
  background-color: #f4c8a6;
  border: solid 1px #e75c3a;
  box-shadow: -3px 4px 0px #e75c3a;
  transition: all 100ms linear;
  cursor: pointer;
}
.btn:hover {
  transform: translate(-3px, 4px);
  box-shadow: none;
}
.btn--start {
  margin-top: 2.25rem;
  font-size: 1.5625rem;
  padding: 0.3125rem 3.25rem;
}
@media (min-width: 48em) {
  .btn--start {
    display: none;
  }
}
.btn--search {
  color: #e75c3a;
  padding: 0.25rem 0.625rem;
  margin-left: 0.375rem;
}

@media (min-width: 48em) {
  .header {
    display: flex;
    align-items: center;
    gap: 1.875rem;
  }
}
.header__search {
  display: none;
}
@media (min-width: 48em) {
  .header__search {
    display: inline-block;
  }
}
@media (min-width: 48em) {
  .header {
    margin-bottom: 3.125rem;
  }
}

.title {
  display: flex;
  gap: 0.3125rem;
  justify-content: center;
  align-items: center;
}
.title__logo {
  width: 2.8125rem;
  height: 2.8125rem;
}
.title__text {
  font-family: "Croissant One", cursive;
  font-size: 2.375rem;
  letter-spacing: -0.25rem;
}

.search__field {
  background-color: #fff;
  border-radius: 1.875rem;
  border: solid 1px #e75c3a;
  box-shadow: -3px 4px 0px #e75c3a;
  padding: 0.25rem 1rem;
  font-family: "Josefin Sans", sans-serif;
  color: #003049;
  letter-spacing: -0.0625rem;
  transition: all 100ms ease-in-out;
  -webkit-appearance: none;
}
.search__field:focus {
  transform: translate(-3px, 4px);
  box-shadow: none;
  border: solid 1px #e75c3a;
  outline: none;
}
.search__field:focus::placeholder {
  visibility: hidden;
}
.search__field::placeholder {
  color: #003049;
}
.search__text {
  display: none;
}

.nav {
  display: none;
}

.recipe {
  margin-top: 2.25rem;
}
@media (min-width: 48em) {
  .recipe {
    margin-top: 0;
  }
}

.results {
  display: none;
}
@media (min-width: 48em) {
  .results {
    display: block;
    background-color: #fff;
    border-radius: 1.875rem;
    border: solid 2px #e75c3a;
    min-width: 18.75rem;
  }
}
.results .message {
  text-align: center;
}
.results .message__title {
  margin-top: 3.75rem;
  font-size: 2.5rem;
  font-weight: 500;
  letter-spacing: -0.125rem;
}
.results .message__text {
  margin: 1.875rem auto;
  font-size: 1.5625rem;
  font-weight: 300;
  width: 15ch;
}

.welcome {
  text-align: center;
}
.welcome__title {
  font-size: 1.75rem;
}
.welcome__text {
  margin-top: 0.9375rem;
  font-size: 1.125rem;
  width: 18ch;
}
.welcome__subtitle {
  font-size: 1.375rem;
  margin-top: 1.75rem;
}
.welcome__features {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.features__item {
  display: flex;
  align-items: center;
  margin-top: 1rem;
  gap: 1.5625rem;
}
.features__icon {
  color: #e75c3a;
  font-size: 1.375rem;
}
.features__text {
  width: 15ch;
  text-align: left;
}